</template>


<script>

    import '../sass/main.scss';
    import 'reset-css';
    import {
        Facebook,
        Twitter,
        Telegram
    } from 'vue-socialmedia-share';

    // import SocialSharing from 'vue-social-sharing'
    let myPathToImg;
    if (document.getElementById('path')) {
        myPathToImg = document.getElementById('path').dataset.path;
    }
    else {
        myPathToImg = '/'
    }
    let answerBack = [];
    if (document.getElementById('path').dataset.answer) {
        answerBack = document.getElementById('path').dataset.answer.split('').map(Number);
    }
    else {
        answerBack = [0, 0, 0, 0, 0, 0, 0, 0, 0];
    }
    console.log(answerBack[2]);


    export default {
        components: {
            Facebook,
            Twitter,
            Telegram
        },
        created() {
            let a = window.location.href;
            var arr = a.match(/dreamhome=(.*)/);
            if (arr != null) { // Did it match?
                this.question = 10;
            }
        },
        name: 'kvartalalley',
        data: function () {
            return {
                question: 0,
                arrfin: [],
                answer: answerBack,
                render: false,
                showButton: true,
                buttonText: "Почати гру",
                path: window.location.href,
                pathtoPhP: myPathToImg,
                pathToImg: myPathToImg + 'img/' || "img",
                progressBarWidth: 0,
                redirect: false
            }
        },
        watch: {
            question: function () {
                if (this.question > 0) {
                    this.buttonText = "Далі"
                    console.log(this.pathToImg)
                }
                if (this.question === 9) {
                    this.buttonText = "Отримати результат тесту";
                    this.redirect = true;
                }
                if (this.question === 10) {
                    this.buttonText = "Дізнатись більше";
                    this.showButton = true;
                    if (this.redirect == true) {
                        window.location = window.location.href + '?dreamhome=' + this.answer.join("");
                        // let info = this.answer.join("");
                        // var bodyFormData = new FormData();
                        // bodyFormData.set('img', info);
                        // axios
                        //     .post(this.pathtoPhP + 'sendMessage.php', bodyFormData).then((response) => {
                        //     window.location = response.data;
                        // })
                    }

                    // window.history.pushState('page2', 'Title', '/result')

                }
                this.leave();
            },
            render: function () {
                this.showHideButton();
            }
        },
        methods: {
            selectAnswer: function (answer) {
                if (answer === 1) {
                    this.$set(this.answer, this.question - 1, 1)
                }
                if (answer === 2) {
                    this.$set(this.answer, this.question - 1, 2)
                }

                if ((this.question === 2)
                    || (this.question === 1 && this.answer[this.question - 1] == 1)
                    || (this.question === 4 && this.answer[this.question - 1] == 1)
                    || (this.question === 5 && this.answer[this.question - 1] == 1)
                    || (this.question === 6 && this.answer[this.question - 1] == 2)
                    || (this.question === 7 && this.answer[this.question - 1] == 1)
                    || (this.question === 8 && this.answer[this.question - 1] == 1)
                    || (this.question === 9 && this.answer[this.question - 1] == 2)
                ) {
                    this.nextQuestion()
                    this.render = false;
                }
                else {
                    this.render = true;
                }


                // if(this.question == 10){
                //     let info= this.answer[0]+""+this.answer[2]+""+this.answer[3]+""+this.answer[7];
                //     console.log(info);
                //     // axios
                //     //     .set("sendMessage.php")
                //     //     .then(response => {
                //     //
                //     //     })
                //     // }
                // }
            },
            nextQuestion: function () {
                if (this.question < 10) {
                    this.question++;
                    this.showButton = false;
                    this.render = false;
                    this.progressBarWidth = this.progressBarWidth + 10;
                    this.$refs.progressbar.style.width = this.progressBarWidth + '%';
                }


            },
            showHideButton: function () {
                if (this.render === true || this.question === 0 || this.question === 10) {
                    this.showButton = true;
                }
                else {
                    this.showButton = false
                }
            },
            backToPreviousStep: function () {
                if (this.render == false) {
                    this.question--;
                    this.render = false;
                    this.$set(this.answer, this.question - 1, 0)
                    this.$set(this.answer, this.question, 0)
                }
                else {
                    this.render = false;
                    this.$set(this.answer, this.question - 1, 0)
                }

            },
            leave: function () {
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });
            },
            doAgain: function () {
                let dreamhome = window.location.href.match(/dreamhome=(.*)/);
                let newurl = window.location.href.replace('?' + dreamhome[0], '')
                window.location = newurl;
            }
        }
    }
</script>

